import React from "react";
import Layout from "../components/Layout";
import Image from "../components/Image";

import { StaticQuery, graphql } from "gatsby";

const Hoodies = () => (
  <StaticQuery
    query={graphql`
      query {
        allHoodiesJson {
          edges {
            node {
              id
              name
              imgs
              sizes
              price
              discount
            }
          }
        }
      }
    `}
    render={data =>
      data.allHoodiesJson.edges.map(({ node }) => (
        <div key={node.id}>
          <div>{node.name}</div>
          <ul>
            {node.imgs.map(img => (
              <li key={img}>
                <Image src={img} />
              </li>
            ))}
          </ul>
          <div>{node.sizes.toString()}</div>
          <div>{node.price}</div>
          <div>{node.discount}</div>
        </div>
      ))
    }
  />
);

export default () => (
  <Layout>
    <Hoodies />
  </Layout>
);